.Portfolio_section {
    width: 100%;


    @media screen and (min-width: $breakpoint-desktop) and (max-width:1171px) {
        width: 100%;
    }

    @media screen and (min-width: 1171px) and (max-width:$Mmax) {
        width: 94%;

    }



    .selectCategory {
        background-position: 50%;
        width: 250px;
        height: 100px;
        margin: 0 auto;
        padding: 0px;
        margin-top: 50px;

        select {
            height: 35px;
            padding: 10px;
            border-radius: 6px;
        }
    }

    .myPortfolio {
        @include flex-column;
        gap: 30px;
        margin-top: 1.5rem;
        margin-bottom: 1.5rem;


        .btg {
            height: 35px;
            width: 240px;
            border-radius: 8px;
            padding: 5px;
        }

        .card {
            border-radius: 10px;
            height: 260px;
            width: 90%;
            cursor: pointer;
            box-shadow: -3px -3px 12px 0 #583e81, 6px 6px 12px 0 rgba(0, 0, 0, .2);
            position: relative;
            transform-style: preserve-3d;

            &:hover {
                .front {
                    transform: rotateY(180deg);
                }

                .back {
                    transform: rotateY(0deg);
                }
            }

            .card-face {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                border-radius: 15px;
                backface-visibility: hidden;
                overflow: hidden;
                border-radius: 10px;

                p {
                    font-size: 13px;
                    margin-bottom: 0;
                    padding: 0 25px 0 25px;
                    display: block;
                    text-align: center;
                    overflow: hidden;
                }
            }

            .front img {
                background-position: center;
                background-size: cover;
                background-repeat: no-repeat;
                width: 100%;
                height: 100%;
                object-fit: fill;
            }

            .back {
                transform: rotateY(180deg);
                @include flex-column;

                .back h2 {
                    padding: 0;
                    font-size: 18px;
                    margin: 0
                }
            }
        }



        @media screen and (min-width: $breakpoint-desktop) and (max-width:1170px) {
            grid-gap: 20px;
            display: grid;
            grid-template-columns: repeat(auto-fill, 400px);
            height: auto;
            justify-content: center;
            margin: 30px auto;
            width: 100%;

            .card {
                height: 320px;
                width: 100%;
                box-shadow: $box-shadow;
                box-shadow: var(--box-shadow);
                box-shadow: -3px -3px 12px 0 #583e81, 6px 6px 12px 0 rgba(0, 0, 0, .2);
            }
        }

        @media screen and (min-width: 1171px) and (max-width:$Mmax) {

            grid-gap: 70px;
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            height: auto;
            justify-content: center;
            padding: 70px;
            width: 100%;
            margin: 0 auto;

            .card {
                border-radius: 15px;
                height: 320px;
                width: 380px;
                position: relative;
                transform-style: preserve-3d;
                background: 0 0;
                box-shadow: -6px -6px 25px 0 #583e81, 6px 6px 25px 0 rgba(0, 0, 0, .2);

                &:hover {
                    .front {
                        transform: rotateY(180deg);
                    }

                    .back {
                        transform: rotateY(0deg);
                    }
                }
            }

            .card-face {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                border-radius: 15px;
                backface-visibility: hidden;
                overflow: hidden;

                p {
                    font-size: 13spx;
                    margin-bottom: 0;
                    padding: 0 25px 0 25px;
                    display: block;
                    text-align: center;
                    overflow: hidden;
                }
            }

            .front img {
                background-position: center;
                background-size: cover;
                background-repeat: no-repeat;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

            .card_title {
                position: absolute;
                inset: auto auto 30px 30px;
                margin: 0;
                transition: inset .3s .3s ease-out;
                font-weight: 400;
                text-transform: uppercase;
                color: #0000;
                font-size: 25px;
                font-weight: bold;
                z-index: 1;
                background-color: rgba(240, 248, 255, .12);
                padding: 20px;
                color: var(--body_color);
            }

            .back {
                transform: rotateY(180deg);
                @include flex-column;

                .back h3 {
                    padding: 0;
                    font-size: 18px;

                }

                p {
                    margin-top: 20px;
                    line-height: 141.8%;
                }
            }
        }

        @media screen and (min-width: 1600px) {
            grid-gap: 70px;
            display: grid;
            grid-template-columns: repeat(auto-fill, 400px);
            height: auto;
            justify-content: center;
            padding: 60px;
            width: 1600px;
            max-width: 100%;
            margin: 0 auto;

            .card {
                border-radius: 15px;
                height: 320px;
                width: 400px;
                position: relative;
                transform-style: preserve-3d;
                background: 0 0;
                box-shadow: -6px -6px 25px 0 #583e81, 6px 6px 25px 0 rgba(0, 0, 0, .2);

                &:hover {
                    .front {
                        transform: rotateY(180deg);
                    }

                    .back {
                        transform: rotateY(0deg);
                    }
                }
            }

            .card-face {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                border-radius: 15px;
                backface-visibility: hidden;
                overflow: hidden;

                p {
                    font-size: 13spx;
                    margin-bottom: 0;
                    padding: 0 25px 0 25px;
                    display: block;
                    text-align: center;
                    overflow: hidden;
                }
            }

            .front img {
                background-position: center;
                background-size: cover;
                background-repeat: no-repeat;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

            .card_title {
                position: absolute;
                inset: auto auto 30px 30px;
                margin: 0;
                transition: inset .3s .3s ease-out;
                font-weight: 400;
                text-transform: uppercase;
                color: #0000;
                font-size: 25px;
                font-weight: bold;
                z-index: 1;
                background-color: rgba(240, 248, 255, .12);
                padding: 20px;
                color: var(--body_color);
            }

            .back {
                transform: rotateY(180deg);
                @include flex-column;

                .back h3 {
                    padding: 0;
                    font-size: 18px;

                }

                p {
                    margin-top: 20px;
                    line-height: 141.8%;
                }
            }
        }
    }
}